@charset "UTF-8";
.price {
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url(./../images/fon.jpg);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  height: 476px; }

.price__wrapper {
  width: 87%;
  padding-top: 5%; }

.price__name {
  height: 100px;
  background-color: #283645;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

.price__kind {
  font-size: 18px;
  color: white;
  font-weight: bold;
  text-transform: uppercase;
  line-height: 1.667; }

.price__cost {
  font-size: 24px;
  color: white;
  text-transform: uppercase;
  line-height: 1.25; }

.price__item--second > .price__name {
  position: relative; }
  .price__item--second > .price__name::before {
    content: "";
    width: 74px;
    height: 73px;
    position: absolute;
    top: 0;
    left: 0;
    transform: rotate(0deg);
    text-align: center;
    background-image: url(./../images/hit.png);
    background-repeat: no-repeat; }
  .price__item--second > .price__name::after {
    content: "Хит";
    position: absolute;
    top: 15%;
    left: 3%;
    transform: rotate(-45deg);
    font-size: 14px;
    line-height: 30px;
    color: #fff;
    text-transform: uppercase; }

.price__item--first .price__option--smile {
  background-image: url(./../images/no.png); }

.price__item--first .price__option--comments {
  background-image: url(./../images/no.png); }

.price__item--second .price__option--smile {
  background-image: url(./../images/yes.png); }

.price__item--second .price__option--comments {
  background-image: url(./../images/no.png); }

.price__item--three .price__option--smile {
  background-image: url(./../images/yes.png); }

.price__item--three .price__option--comments {
  background-image: url(./../images/yes.png); }

.price__option {
  height: 60px;
  border: 2px solid #e5e5e5;
  background-color: #fff;
  font-size: 14px;
  color: #283645;
  text-transform: uppercase;
  line-height: 2.143;
  display: flex;
  align-items: center;
  padding-left: 5%;
  background-position-x: 90%;
  background-position-y: center;
  background-repeat: no-repeat; }

.price__option--filter {
  background-image: url(./../images/yes.png); }

.price__name {
  border: 2px solid #e5e5e5; }

.price__options--tablet,
.price__name--tablet,
.price__item--tablet {
  display: none; }

.price__list > .owl-dots {
  margin-top: 40px; }

@media (min-width: 660px) {
  .price__wrapper {
    width: 94%; }
  .price__list {
    display: flex;
    width: 100%; }
  .price__list > .item {
    width: 34%; }
  .price__options--tablet,
  .price__name--tablet,
  .price__item--tablet {
    display: flex;
    flex-direction: column; }
  .price__name--tablet {
    background-color: transparent;
    border: none;
    height: 104px; }
  .price__item--tablet {
    width: 33%; }
  .price__items {
    display: flex;
    align-items: center; }
  .price__item--first .price__option--filter,
  .price__item--first .price__option--smile,
  .price__item--first .price__option--comments {
    font-size: 0;
    background-position-x: center;
    background-position-y: center; }
  .price__item--second .price__option--filter,
  .price__item--second .price__option--smile,
  .price__item--second .price__option--comments {
    font-size: 0;
    background-position-x: center;
    background-position-y: center; }
  .price__item--three .price__option--filter,
  .price__item--three .price__option--smile,
  .price__item--three .price__option--comments {
    font-size: 0;
    background-position-x: center;
    background-position-y: center; }
  .price__options--tablet .price__option--filter {
    background-image: none; }
  .price__kind {
    font-size: 16px; }
  .price__cost {
    font-size: 18px; } }

@media (min-width: 1200px) {
  .price__wrapper {
    width: 80%; }
  .price__kind {
    font-size: 18px; }
  .price__cost {
    font-size: 24px; } }
